<template>
    <div class="tetris">
      <div class="background">
        <div class="text-container" aria-hidden="true">
          <span v-for="n in 20" :key="n">Hannim</span>
        </div>
      </div>
      <div class="canvas-container">
        <canvas ref="gameBoard" width="300" height="600"></canvas>
        <div class="neon-border"></div>
      </div>
      <div class="game-info">
        <p>Ï†êÏàò: {{ score }}</p>
        <button @click="startGame">Í≤åÏûÑ ÏãúÏûë</button>
      </div>
    </div>
  </template>
  
  <script setup>
  import { ref, onMounted, onUnmounted } from 'vue';
  
  // ÏÉÅÏàò Ï†ïÏùò
  const BLOCK_SIZE = 30;
  const BOARD_WIDTH = 10;
  const BOARD_HEIGHT = 20;
  
  // ÌÖåÌä∏Î¶¨Ïä§ Î∏îÎ°ù Î™®Ïñë Ï†ïÏùò
  const SHAPES = [
    [[1, 1, 1, 1]],     // I Î™®Ïñë
    [[1, 1], [1, 1]],   // O Î™®Ïñë
    [[1, 1, 1], [0, 1, 0]], // T Î™®Ïñë
    [[1, 1, 1], [1, 0, 0]], // L Î™®Ïñë
    [[1, 1, 1], [0, 0, 1]], // J Î™®Ïñë
    [[1, 1, 0], [0, 1, 1]], // S Î™®Ïñë
    [[0, 1, 1], [1, 1, 0]]  // Z Î™®Ïñë
  ];
  
  // Î∞òÏùëÌòï ÏÉÅÌÉú Ï†ïÏùò
  const gameBoard = ref(null);
  const ctx = ref(null);
  const score = ref(0);
  const gameInterval = ref(null);
  
  // Í≤åÏûÑ ÏÉÅÌÉú
  const gameState = ref({
    board: Array(BOARD_HEIGHT).fill().map(() => Array(BOARD_WIDTH).fill(0)),
    currentPiece: null,
    currentPieceX: 0,
    currentPieceY: 0
  });
  
  // Í≤åÏûÑ Í¥ÄÎ†® Î©îÏÑúÎìúÎì§
  const createPiece = () => {
    const shape = SHAPES[Math.floor(Math.random() * SHAPES.length)];
    gameState.value.currentPiece = shape;
    gameState.value.currentPieceX = Math.floor(BOARD_WIDTH / 2) - Math.floor(shape[0].length / 2);
    gameState.value.currentPieceY = 0;
  };
  
  const collision = (x = gameState.value.currentPieceX, 
                    y = gameState.value.currentPieceY, 
                    piece = gameState.value.currentPiece) => {
    for (let r = 0; r < piece.length; r++) {
      for (let c = 0; c < piece[r].length; c++) {
        if (piece[r][c]) {
          const newX = x + c;
          const newY = y + r;
          
          if (newX < 0 || newX >= BOARD_WIDTH || 
              newY >= BOARD_HEIGHT ||
              (newY >= 0 && gameState.value.board[newY][newX])) {
            return true;
          }
        }
      }
    }
    return false;
  };
  
  const getGhostPosition = () => {
    let ghostY = gameState.value.currentPieceY;
    
    while (!collision(gameState.value.currentPieceX, ghostY + 1, gameState.value.currentPiece)) {
      ghostY++;
    }
    
    return ghostY;
  };
  
  // drawBoard Ìï®Ïàò ÏàòÏ†ï
  const drawBoard = () => {
    if (!ctx.value) return;
    
    // Ïö∞Ï£º Î∞∞Í≤Ω Í∑∏Î¶¨Í∏∞
    const gradient = ctx.value.createLinearGradient(0, 0, 0, gameBoard.value.height);
    gradient.addColorStop(0, '#0B0B3B');  // ÏßÑÌïú ÎÇ®ÏÉâ
    gradient.addColorStop(1, '#000033');  // Ïñ¥ÎëêÏö¥ ÎÇ®ÏÉâ
    ctx.value.fillStyle = gradient;
    ctx.value.fillRect(0, 0, gameBoard.value.width, gameBoard.value.height);
    
    // Î≥Ñ Í∑∏Î¶¨Í∏∞
    for (let i = 0; i < 50; i++) {
      const x = Math.random() * gameBoard.value.width;
      const y = Math.random() * gameBoard.value.height;
      const radius = Math.random() * 1.5;
      const opacity = Math.random();
      
      ctx.value.beginPath();
      ctx.value.arc(x, y, radius, 0, Math.PI * 2);
      ctx.value.fillStyle = `rgba(255, 255, 255, ${opacity})`;
      ctx.value.fill();
    }
    
    // Í∏∞Ï°¥ Î∏îÎ°ù Í∑∏Î¶¨Í∏∞
    for (let y = 0; y < BOARD_HEIGHT; y++) {
      for (let x = 0; x < BOARD_WIDTH; x++) {
        if (gameState.value.board[y][x]) {
          ctx.value.fillStyle = '#4169E1';  // Î°úÏñÑ Î∏îÎ£®
          ctx.value.fillRect(x * BLOCK_SIZE, y * BLOCK_SIZE, BLOCK_SIZE - 1, BLOCK_SIZE - 1);
          // Î∏îÎ°ù ÎÇ¥Î∂Ä Í¥ëÏ±Ñ Ìö®Í≥º
          const glow = ctx.value.createRadialGradient(
            x * BLOCK_SIZE + BLOCK_SIZE/2,
            y * BLOCK_SIZE + BLOCK_SIZE/2,
            0,
            x * BLOCK_SIZE + BLOCK_SIZE/2,
            y * BLOCK_SIZE + BLOCK_SIZE/2,
            BLOCK_SIZE/2
          );
          glow.addColorStop(0, 'rgba(255, 255, 255, 0.3)');
          glow.addColorStop(1, 'rgba(255, 255, 255, 0)');
          ctx.value.fillStyle = glow;
          ctx.value.fillRect(x * BLOCK_SIZE, y * BLOCK_SIZE, BLOCK_SIZE - 1, BLOCK_SIZE - 1);
        }
      }
    }
    
    // Í∑∏Î¶ºÏûê Í∑∏Î¶¨Í∏∞ (ÏïΩÍ∞Ñ Îçî Î∞ùÏùÄ ÏÉâÏÉÅÏúºÎ°ú)
    if (gameState.value.currentPiece) {
      const ghostY = getGhostPosition();
      ctx.value.fillStyle = 'rgba(100, 149, 237, 0.3)';  // Î∞ùÏùÄ ÌååÎûÄÏÉâ
      for (let y = 0; y < gameState.value.currentPiece.length; y++) {
        for (let x = 0; x < gameState.value.currentPiece[y].length; x++) {
          if (gameState.value.currentPiece[y][x]) {
            ctx.value.fillRect(
              (gameState.value.currentPieceX + x) * BLOCK_SIZE,
              (ghostY + y) * BLOCK_SIZE,
              BLOCK_SIZE - 1,
              BLOCK_SIZE - 1
            );
          }
        }
      }
    }
    
    // ÌòÑÏû¨ Î∏îÎ°ù Í∑∏Î¶¨Í∏∞ (ÎÑ§Ïò® Ìö®Í≥º Ï∂îÍ∞Ä)
    if (gameState.value.currentPiece) {
      ctx.value.fillStyle = '#00FFFF';  // ÏãúÏïàÏÉâ
      for (let y = 0; y < gameState.value.currentPiece.length; y++) {
        for (let x = 0; x < gameState.value.currentPiece[y].length; x++) {
          if (gameState.value.currentPiece[y][x]) {
            ctx.value.fillRect(
              (gameState.value.currentPieceX + x) * BLOCK_SIZE,
              (gameState.value.currentPieceY + y) * BLOCK_SIZE,
              BLOCK_SIZE - 1,
              BLOCK_SIZE - 1
            );
            // ÎÑ§Ïò® Í∏ÄÎ°úÏö∞ Ìö®Í≥º
            ctx.value.shadowColor = '#00FFFF';
            ctx.value.shadowBlur = 10;
            ctx.value.fillRect(
              (gameState.value.currentPieceX + x) * BLOCK_SIZE,
              (gameState.value.currentPieceY + y) * BLOCK_SIZE,
              BLOCK_SIZE - 1,
              BLOCK_SIZE - 1
            );
            ctx.value.shadowBlur = 0;
          }
        }
      }
    }
  };
  
  const mergePiece = () => {
    for (let y = 0; y < gameState.value.currentPiece.length; y++) {
      for (let x = 0; x < gameState.value.currentPiece[y].length; x++) {
        if (gameState.value.currentPiece[y][x]) {
          gameState.value.board[gameState.value.currentPieceY + y][gameState.value.currentPieceX + x] = 1;
        }
      }
    }
  };
  
  const checkLines = () => {
    let linesToRemove = [];
    
    for (let y = BOARD_HEIGHT - 1; y >= 0; y--) {
      if (gameState.value.board[y].every(cell => cell)) {
        linesToRemove.push(y);
      }
    }
    
    if (linesToRemove.length > 0) {
      let flashCount = 0;
      const flash = setInterval(() => {
        flashCount++;
        
        linesToRemove.forEach(y => {
          for (let x = 0; x < BOARD_WIDTH; x++) {
            ctx.value.fillStyle = flashCount % 2 === 0 ? 'white' : 'yellow';
            ctx.value.fillRect(
              x * BLOCK_SIZE,
              y * BLOCK_SIZE,
              BLOCK_SIZE - 1,
              BLOCK_SIZE - 1
            );
          }
        });
        
        if (flashCount >= 4) {
          clearInterval(flash);
          linesToRemove.forEach(y => {
            gameState.value.board.splice(y, 1);
            gameState.value.board.unshift(Array(BOARD_WIDTH).fill(0));
          });
          
          score.value += linesToRemove.length * 100;
          drawBoard();
        }
      }, 100);
    }
  };
  
  const hardDrop = () => {
    while (!collision()) {
      gameState.value.currentPieceY++;
    }
    gameState.value.currentPieceY--;
    mergePiece();
    checkLines();
    createPiece();
    
    if (collision()) {
      clearInterval(gameInterval.value);
      alert('Í≤åÏûÑ Ïò§Î≤Ñ! Ï†êÏàò: ' + score.value);
    }
    
    drawBoard();
  };
  
  const gameLoop = () => {
    gameState.value.currentPieceY++;
    
    if (collision()) {
      gameState.value.currentPieceY--;
      mergePiece();
      checkLines();
      createPiece();
      
      if (collision()) {
        clearInterval(gameInterval.value);
        alert('Í≤åÏûÑ Ïò§Î≤Ñ! Ï†êÏàò: ' + score.value);
        return;
      }
    }
    
    drawBoard();
  };
  
  const startGame = () => {
    gameState.value.board = Array(BOARD_HEIGHT).fill().map(() => Array(BOARD_WIDTH).fill(0));
    score.value = 0;
    createPiece();
    if (gameInterval.value) clearInterval(gameInterval.value);
    gameInterval.value = setInterval(gameLoop, 1000);
  };
  
  // ÌÇ§Î≥¥Îìú Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨ ÏàòÏ†ï (Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ)
  const handleKeydown = (e) => {
    if (!gameState.value.currentPiece) return;
    
    // Ïä§ÌéòÏù¥Ïä§Î∞î Ïä§ÌÅ¨Î°§ Î∞©ÏßÄ
    if (e.key === ' ') {
      e.preventDefault();
    }
    
    switch (e.key) {
      case 'ArrowLeft':
        e.preventDefault();
        gameState.value.currentPieceX--;
        if (collision()) gameState.value.currentPieceX++;
        break;
      case 'ArrowRight':
        e.preventDefault();
        gameState.value.currentPieceX++;
        if (collision()) gameState.value.currentPieceX--;
        break;
      case 'ArrowDown':
        e.preventDefault();
        gameState.value.currentPieceY++;
        if (collision()) gameState.value.currentPieceY--;
        break;
      case 'ArrowUp': {
            e.preventDefault();
            const rotated = gameState.value.currentPiece[0].map((_, i) => 
            gameState.value.currentPiece.map(row => row[i]).reverse()
            );
            const previousPiece = gameState.value.currentPiece;
            gameState.value.currentPiece = rotated;
            if (collision()) gameState.value.currentPiece = previousPiece;
        }
        break;
      case ' ':
        hardDrop();
        break;
    }
    
    drawBoard();
  };
  
  // ÎùºÏù¥ÌîÑÏÇ¨Ïù¥ÌÅ¥ ÌõÖ
  onMounted(() => {
    ctx.value = gameBoard.value.getContext('2d');
    window.addEventListener('keydown', handleKeydown);
  });
  
  onUnmounted(() => {
    if (gameInterval.value) clearInterval(gameInterval.value);
    window.removeEventListener('keydown', handleKeydown);
  });
  </script>
  
  <style scoped>
.tetris {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  background-color: #000033;
}

.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
}

.text-container {
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  animation: slide 40s linear infinite;
  transform: rotate(-45deg);
}

.text-container span {
  position: absolute;
  color: rgba(255, 255, 255, 0.1);
  font-size: 2rem;
  font-family: 'Arial', sans-serif;
  white-space: nowrap;
  text-transform: uppercase;
  font-weight: bold;
}

/* ÌÖçÏä§Ìä∏ ÏúÑÏπò ÏÑ§Ï†ï */
.text-container span:nth-child(1) { top: 5%; left: 5%; }
.text-container span:nth-child(2) { top: 15%; left: 25%; }
.text-container span:nth-child(3) { top: 25%; left: 45%; }
.text-container span:nth-child(4) { top: 35%; left: 65%; }
.text-container span:nth-child(5) { top: 45%; left: 85%; }
.text-container span:nth-child(6) { top: 55%; left: 5%; }
.text-container span:nth-child(7) { top: 65%; left: 25%; }
.text-container span:nth-child(8) { top: 75%; left: 45%; }
.text-container span:nth-child(9) { top: 85%; left: 65%; }
.text-container span:nth-child(10) { top: 95%; left: 85%; }

@keyframes slide {
  0% {
    transform: rotate(-45deg) translateY(0);
  }
  100% {
    transform: rotate(-45deg) translateY(100%);
  }
}

.canvas-container {
  position: relative;
  padding: 3px;
  z-index: 1;
}

/* ÎÑ§Ïò® ÌÖåÎëêÎ¶¨ Ïä§ÌÉÄÏùº */
.neon-border {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 2px solid #00FFFF;
  box-shadow: 
    0 0 5px #00FFFF,
    0 0 10px #00FFFF,
    0 0 15px #00FFFF,
    0 0 20px #00FFFF;
  pointer-events: none;
}

.game-info {
  position: relative;
  z-index: 1;
  text-align: center;
  color: #00FFFF;
  text-shadow: 0 0 5px #00FFFF;
}

/* Î≥ÑÎπõ Ìö®Í≥º */
.tetris::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 4px),
    radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 3px),
    radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 4px);
  background-size: 550px 550px, 350px 350px, 250px 250px;
  background-position: 0 0, 40px 60px, 130px 270px;
  z-index: 0;
  animation: twinkle 15s linear infinite;
}

@keyframes twinkle {
  0% { opacity: 0.5; }
  50% { opacity: 1; }
  100% { opacity: 0.5; }
}

button {
  background-color: transparent;
  border: 2px solid #00FFFF;
  color: #00FFFF;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 0 5px #00FFFF;
}

button:hover {
  background-color: #00FFFF;
  color: #000;
  box-shadow: 
    0 0 10px #00FFFF,
    0 0 20px #00FFFF;
}

@media (max-width: 768px) {
  .text-container span {
    font-size: 1.5rem;
  }
}
</style>