<template>
  <div style="margin: 15px;">
    <el-row :gutter="20">
      <el-col :span="24">
        <h4>Í∏∞Ï§ÄÏùº</h4>
        <div style="margin-bottom: 20px;">
          <el-radio-group v-model="rdoValType" @change="changeRadio">
            <el-radio-button label="PROC_YM" value="procym" />
            <el-radio-button label="PROC_DATE" value="procdate" />
            <el-radio-button label="ÎÇ†Ïßú" value="date" />
          </el-radio-group>
        </div>
        <el-date-picker
          v-if="rdoValType === 'date'"
          v-model="input1"
          type="date"
          value-format="YYYY-MM-DD"
          placeholder="YYYY-MM-DD"
          ></el-date-picker>
        <el-input
          v-if="rdoValType !== 'date'"
          v-model="input1"></el-input>

        <el-button type="success" :icon="Refresh" style="margin-bottom: 20px;" @click="refresh"></el-button>
      </el-col>
    </el-row>
    <el-row>
      <div class="mb-4">
        <!-- <el-button type="primary" round @click="changeDate('Ìà¨Ï∫êÎ¶≠ÌÑ∞')">TO_CHAR</el-button> -->
        <el-button type="primary" round @click="changeDate('Ìà¨Ï∫êÎ¶≠ÌÑ∞', selectedOption)" style="position: relative; margin: 4px;">TO_CHAR&nbsp;&nbsp;
          <el-select
            v-model="selectedOption"
            size="small"
            style="width: 150px;"
            @change="changeToCharFormat"
            @click.stop
          >
            <el-option label="YYYY-MM-DD" value="YYYY-MM-DD"></el-option>
            <el-option label="YYYYMMDD" value="YYYYMMDD"></el-option>
            <el-option label="YYYYMM" value="YYYYMM"></el-option>
          </el-select>
        </el-button>

        <el-button type="primary" round style="margin: 4px;" @click="changeDate('Ìà¨Îç∞Ïù¥Ìä∏')">TO_DATE</el-button>
        <el-button type="primary" round style="margin: 4px;" @click="changeDate('1ÏùºÌà¨Îç∞Ïù¥Ìä∏')">1Ïùº</el-button>
        <el-button type="primary" round style="margin: 4px;" @click="changeDate('ÎßêÏùº')">ÎßêÏùº</el-button>
        <el-button type="primary" round style="margin: 4px;" @click="changeDate('1ÏùºÏãúÎ∂ÑÏ¥à')">1ÏùºÏãúÎ∂ÑÏ¥à</el-button>
        <el-button type="primary" round style="margin: 4px;" @click="changeDate('ÏãúÎ∂ÑÏ¥à')">ÏãúÎ∂ÑÏ¥à</el-button>
        <el-button type="primary" round style="margin: 4px;" @click="changeDate('Ïï†ÎìúÎ®ºÏä§')">ADD_MONTHS</el-button>
        <el-button type="primary" round style="margin: 4px;" @click="changeDate('ÎßâÎåÄÍ∏∞ÏóÜÏï†Í∏∞')">ÎßâÎåÄÍ∏∞ÏóÜÏï†Í∏∞</el-button>
        <el-button type="primary" round style="margin: 4px;" @click="changeDate('Ìä∏Î†ÅÌÅ¨')">TRUNC</el-button>
        <el-button type="primary" round style="margin: 4px;" @click="changeDate('ÏΩòÏ∫£')">CONCAT</el-button>
        <el-button type="primary" round style="margin: 4px;" @click="changeDate('YYYYMMÏûêÎ•¥Í∏∞')">YYYYMMÏûêÎ•¥Í∏∞</el-button>
      </div>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div
          style="display: flex;
                align-items: center;
                gap: 15px;">
          <el-button type="primary" :icon="ArrowLeftBold" circle @click="undo"> </el-button>
          <el-button type="primary" circle @click="copyToClipboard">
            <el-icon>
              <component :is="copiedStates ? 'Select' : 'DocumentCopy'" />
            </el-icon>
          </el-button>
          <div
            style="border-radius: var(--el-border-radius-base);
                  border: 1px solid var(--el-border-color);
                  word-wrap: break-word;
                  overflow-wrap: break-word;
                  padding: 10px;
                  flex: 1;">
            <p v-html="resultValue"></p>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        {{ changeHistoryKR.join(' >') }}
      </el-col>
    </el-row>
  </div>
  <!-- <PROC_YM :proc_ym="input1"></PROC_YM> -->
</template>

<script setup>
import { ref, watch } from 'vue'
import { ArrowLeftBold, DocumentCopy, Refresh } from '@element-plus/icons-vue'
// import PROC_YM from './DateWizard_ProcYM.vue'
// import PROC_DATE from './DateWizard_ProcDate.vue'
import {format} from 'date-fns'
import DateUtil from '@/utils/dateUtil'
import { ElMessage } from 'element-plus'

const rdoValType = ref('procym') //ÎùºÎîîÏò§Î≤ÑÌäº
const input1 = ref('P_PROC_YM') //Í∏∞Ï§ÄÍ∞í
const today = new Date()
const resultValue = ref(input1.value)
const resultText = ref(input1.value)
const changeHistory = ref([])
const changeHistoryKR = ref([])
const copiedStates = ref(false) // Í∞ÅÍ∞ÅÏùò Î≤ÑÌäº ÏÉÅÌÉúÎ•º Ï†ÄÏû•Ìï† Î∞∞Ïó¥
const selectedOption = ref('YYYY-MM-DD')


watch(input1, (newName) => {
    if(changeHistory.value.length > 0){
      resultValue.value = changeHistory.value.at(-1).html.replace('#val', newName)
    } else {
      resultValue.value = input1.value
      resultText.value = input1.value
    }
  }
)

//to_charÏóê select ÏòµÏÖò Î≥ÄÍ≤Ω
const changeToCharFormat = (val) => {
  selectedOption.value = val
}

//Ïπ¥ÌîºÌïòÍ∏∞
const copyToClipboard = () => {
  const text = resultText.value.replace('#val', rdoValType.value === 'date' ? `'${input1.value}'` : input1.value)
  navigator.clipboard.writeText(text).then(() => {
    copiedStates.value = true // ÌÅ¥Î¶≠Ìïú Î≤ÑÌäºÏùò ÏÉÅÌÉúÎ•º trueÎ°ú ÏÑ§Ï†ï
    setTimeout(() => {
      copiedStates.value = false // 2Ï¥à ÌõÑ ÏõêÎûò ÏÉÅÌÉúÎ°ú Î≥µÏõê
    }, 2000)
    ElMessage({
      message: 'ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóê Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§',
      type: 'success'
    })
  }).catch((err) => {
    console.error('ÌÅ¥Î¶ΩÎ≥¥Îìú Î≥µÏÇ¨ Ïã§Ìå®: ', err)
  });
};

//Ï¥àÍ∏∞Ìôî
const refresh = () => {
  rdoValType.value = 'procym' //ÎùºÎîîÏò§Î≤ÑÌäº
  input1.value = 'P_PROC_YM'
  changeHistory.value = []
  changeHistoryKR.value = []
  resultValue.value = input1.value
  resultText.value = input1.value
}

//ÎùºÎîîÏò§Î≥ÄÍ≤Ω
const changeRadio = () => {
  if(rdoValType.value === 'procym') {
    input1.value = 'P_PROC_YM'
    /* eslint-disable no-debugger */
    // debugger
    /* eslint-enable no-debugger */
  } else if (rdoValType.value === 'procdate') {
    input1.value = 'P_PROC_DATE'
  } else {
    
    input1.value = format(today, 'yyyy-MM-dd')
    /* eslint-disable no-debugger */
    // debugger
    /* eslint-enable no-debugger */
  }
}

//ÎÇ†Ïßú Î≥ÄÌôò
const changeDate = (type, format) => {
  const result = DateUtil.changeDate(type, resultValue.value, input1.value, format == null ? 'YYYY-MM-DD' : format)
      resultValue.value = result.toSpan().replace('#val', input1.value)
      resultText.value = DateUtil.changeDate(type, resultText.value, input1.value, format == null ? 'YYYY-MM-DD' : format).toText()

      changeHistory.value.push({
        html: result.toSpan(),
        plainText: resultText.value
      })

      changeHistoryKR.value.push(type)
}

//ÎêòÎèåÎ¶¨Í∏∞
const undo = () => {
  if(changeHistory.value.length > 1) {
    changeHistory.value.pop()
    changeHistoryKR.value.pop()

    resultValue.value = changeHistory.value.at(-1).html.replace('#val', input1.value)
    resultText.value = changeHistory.value.at(-1).plainText  
  } else if (changeHistory.value.length == 1) {
    changeHistory.value.pop()
    changeHistoryKR.value.pop()

    resultValue.value = input1.value
    resultText.value = input1.value
  }
}

</script>

<style>
.el-row{
  margin-bottom: 20px;
}

.el-row:last-child {
  margin-bottom: 0;
}

.el-input{
  width: 400px;
  margin-bottom: 20px;
  margin-right: 20px;
}

</style>